/**
 * Add two numbers
 * @customfunction 
 * @param {number} first First number
 * @param {number} second Second number
 * @returns {number} The sum of the two numbers.
 */
function add(first, second) {
  return first + second;
}

/**
 * Displays the current time once a second
 * @customfunction 
 * @param {CustomFunctions.StreamingInvocation<string>} invocation Custom function invocation
 */
function clock(invocation) {
  const timer = setInterval(() => {
    const time = currentTime();
    invocation.setResult(time);
  }, 1000);

  invocation.onCanceled = () => {
    clearInterval(timer);
  };
}

/**
 * Returns the current time
 * @returns {string} String with the current time formatted for the current locale.
 */
function currentTime() {
  return new Date().toLocaleTimeString();
}

/**
 * Increments a value once a second.
 * @customfunction 
 * @param {number} incrementBy Amount to increment
 * @param {CustomFunctions.StreamingInvocation<number>} invocation
 */
function increment(incrementBy, invocation) {
  let result = 0;
  const timer = setInterval(() => {
    result += incrementBy;
    invocation.setResult(result);
  }, 1000);

  invocation.onCanceled = () => {
    clearInterval(timer);
  };
}

/**
 * Writes a message to console.log().
 * @customfunction LOG
 * @param {string} message String to write.
 * @returns String to write.
 */
function logMessage(message) {
  console.log(message);
  console.log("Cheese")
  return message;
}


/**
 * Gets a login token.
 * @customfunction getToken
 * @param {string} user User name
 * @param {string} pwd password
 * @param {string} clientId client id
 * @param {string} domain domain
 * @returns Token to write.
 */
async function getToken(user, pwd, clientId, domain) {
  try {

    console.log("Getting token...")

    var myHeaders = new Headers();
    myHeaders.append("Content-Type", "application/x-www-form-urlencoded");

    var urlencoded = new URLSearchParams();
    urlencoded.append("username", user);
    urlencoded.append("password", pwd);
    urlencoded.append("grant_type", "password");
    urlencoded.append("client_id", clientId);

    var requestOptions = {
      method: 'POST',
      headers: myHeaders,
      body: urlencoded,
      redirect: 'follow'
    };


    let response = await fetch( domain + "/connect/token", requestOptions);

    if (!response.ok) {
      console.log(response);
      return "Response Error: " + response.statusText
    }

    console.log(response.headers);
    
    let body = await response.json();

    let token = body.access_token
    return new Date().toLocaleTimeString() + ":" + token;

  }
  catch (error) {
    console.log("Error here");
    console.log(error);
    return error.toString(); 
  }
  
}